.homeProduct {
    letter-spacing: 1.2px;

    & a {
        text-decoration: none;
        color: rgb(59, 59, 59);
    }

    & .productImg {
        text-align: center;

        & img {
            width: 100%;
            max-width: 250px;
            max-height: 250px;
        }
    }

    & .productabout {
        padding: 20px;
        display: flex;
        justify-content: space-between;
        flex-direction: column;

        & span {
            color: gray
        }

        ;
    }
}

.forShodowHomeproduct :hover img {
    scale: 1.07;
    transform: translateY(-10px);
}

.forShodowHomeproduct :hover img {
    transition: all .2s ease-in-out;
}

.actionbtn {
    display: flex;
    justify-content: space-between;
    padding: 0px 20px;

    & button {
        width: 100%;
        max-width: 120px;
        padding: 5px;
        border: none;
        border-radius: 10px;
        background-color: forestgreen;
        color: rgb(251, 251, 251);
        font-weight: 600;
        letter-spacing: 1.2px;
        transition: all .2s ease-in-out;

        &:hover {
            scale: 1.05;
            color: rgba(0, 0, 0, 0.701);
        }

    }
}