/* Announcement Bar Styles */
.announcement-bar {
  background-color: rgb(251, 26, 26);
  color: white;
  padding: 6px 10px;
  text-align: center;
  font-size: 10px;
}

@media(max-width: 500px) {
  .announcement-bar {
    font-size: 7px;
  }
}

.announcement-list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.announcement-list li {
  margin: 0 10px;
}

.announcement-list a {
  color: white;
  text-decoration: none;
  font-weight: bold;
  letter-spacing: 1.01px;
}

.announcement-list a:hover {
  text-decoration: underline;
}

.nav-logo {
  height: 50px;
  width: auto;
}

.navbar {
  background: rgb(42, 195, 34);
  background: linear-gradient(276deg, rgba(42, 195, 34, 0.245) 15%, rgba(67, 180, 9, 0.5466561624649859) 100%);
  box-shadow: 0px 4px 6px rgba(74, 199, 76, 0);
}

.search-bar {
  max-width: 100%;
}

.cart-adminbtn {
  display: flex;
  align-items: center;
}

.cartBtn {
  margin-right: 10px;
  border: none;
  color: #59a30e;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2px;
  padding: 3px 10px;
  background-color: #fff;

  &:hover {
    background-color: #59a30e;
    color: white;
  }

}


.adminbtn {
  color: #03a6e3;
  background-color: #fff;
  padding: 9px 10px;
  border: none;
  border-radius: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  font-weight: bold;

  &:hover {
    background-color: #59a30e;
    color: white;
  }
}

.Navbar-menu-Bar {
  background: rgb(42, 195, 34);
  background: linear-gradient(276deg, rgba(42, 195, 34, 0.245) 15%, rgba(67, 180, 9, 0.5466561624649859) 100%);
  display: block;

  & .menuOption {
    padding: 0px 20px 10px 20px;

    & a {
      text-decoration: none;
      color: rgba(0, 0, 0, 0.696);
    }

    & ul {
      letter-spacing: 1.2px;
      display: flex;
      justify-content: space-around;
      line-height: 25px;
      font-size: 14px;
      font-weight: 500;
      margin: 0;
      padding: 0;
      color: rgb(65, 64, 64);
      transition: all 3s ease-in-out;
      z-index: 9999;

      & li {
        list-style: none;
        margin: 0;
        padding: 0;
        cursor: pointer;

        & a:hover {
          color: #03a6e3;
        }

      }
    }
  }
}



@media (max-width: 768px) {
  .Navbar-menu-Bar {
    display: none;
  }

  .Navbar-menu-Bar.show {
    display: block;

    & ul {
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }

  .cart-adminbtn {
    flex-direction: row;
    gap: 5px;
  }

  .search-bar {
    margin-top: 10px;
  }
}

@media (max-width:425px) {
  .nav-logo {
    width: auto;
    height: 40px;
  }

}

@media (max-width:400px) {
  .nav-logo {
    width: auto;
    height: 30px;
  }

  .adminbtn {
    padding: 5px 7px;

  }

}