.detailsSection img {
    width: 100%;
    max-width: 400px;
    max-height: 400px;
}

.home-pro-more-details {
    padding: 30px;

    & .h-d-price {
        font-size: 1.6rem;

        & .special-tag {
            font-size: 12px;
            background-color: forestgreen;
            padding: 1px 5px;
            color: white;
            font-weight: 600;
            letter-spacing: 1.04px;
            border-radius: 5px;
        }
    }

    & .ratingIcon-d {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        gap: 3px;
        color: gold;

        & span {
            color: gray;
            font-weight: 600;
            letter-spacing: 1.04px;
            font-size: 14px;
        }
    }

    .h-p-d-d {
        color: rgb(86, 87, 87);
        font-size: 15px;
        font-weight: 600;
    }
}

.h-pro-d-priceSection {
    display: flex;
    justify-content: flex-start;
    align-items: flex-end;
    gap: 5px;
}

.h-pro-d-stockAlert-low div:nth-child(1) {
    color: red;
    font-size: 14px;
}
.h-pro-d-stockAlert-high div:nth-child(1) {
    color: rgba(0, 0, 0, 0.637);
    font-size: 14px;
    font-weight: 600;
}

.h-pro-d-stockAlert-low ,.h-pro-d-stockAlert-high div:nth-child(2) {
    color: gray;
    font-size: 14px;
    font-weight: 600;
    letter-spacing: 1.01px;
}


.home-p-container .addcartBtn-buyBtn {
    display: flex;
    justify-content: flex-start;
    gap: 30px;

    & button {
        padding: 7px 18px;
        background-color: #0fba6a;
        color: white;
        font-weight: 600;
        border: none;
        border-radius: 10px;
        display: flex;
        align-items: center;
        gap: 3px;
    }
}

