/* Suggest Product CSS */
.suggestItemcard {
    & .suggestItemTitle {
        color: #0fba6a;
    }

    & .suggestDescription {
        color: #555555;
        font-weight: 600;

    }

    &:hover img {
        scale: 1.05;
    }
}

.suggestItemcard .suggestImg img {
    width: 100%;
    max-width: 200px;

}

.suggestItemcard .suggestDescription p {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}










@media (max-width: 425px) {
    .suggestItemcard {
        display: flex;
        justify-content: center;
        text-align: center;
        align-items: center;
    }

    .suggestItemcard .suggestImg {
        padding: 20px;
       
    }

    .suggestItemcard .suggestDescription p {
        overflow: unset;
        text-overflow: unset;
        white-space: inherit;
    }
}

.suggestImg img{
    max-width: 200px;
    max-height: 210px;
}